.page {
    width: 335px;
    // min-height: 200px;
    margin: 0 auto;
    margin-top: 24px;

    &>.page-title {
        font-size: 20px;
        font-family: PingFangSC-Semibold, PingFang SC;
        font-weight: 600;
        color: #2B2B2B;
        // margin-top: -20px;

        .page-desc {
            width: 335px;
            height: 34px;
            font-size: 12px;
            font-family: PingFangSC-Regular, PingFang SC;
            font-weight: 400;
            color: #999999;
            line-height: 17px;
            margin-top: 8px;
        }
    }

    .page-subject {
        width: 335px;
        min-height: 597px;
        background: #FFFFFF;
        box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.05);
        border-radius: 10px;
        margin-top: 25px;
        margin-bottom: 30px;
        padding: 20px 20px 30px 20px;
        &>.serviceCharge{
            margin: 0 auto;
            margin-top: 20px;
            font-size: 13px;
            color: #666666;
        }
        &>.ant-select {
            &>.ant-select-arrow {
                top: 36%;
            }
        }
        &>.transition_view{
            width: 295px;
            margin-top: 20px;
            margin-bottom: 20px;
            &>.trans_start{
                display: flex;
                justify-content: space-between;
                margin-bottom: 10px;
                &>div{
                    width: 122px;
                    text-align: left;
                    font-size: 14px;
                    font-weight: 400;
                    color: #666666;
                }
            }
            &>.trans_deal{
                display: flex;
                justify-content: space-between;
                align-items: center;
                &>.deal_style{
                    width: 122px;
                    height: 108px;
                    background: #FFFFFF;
                    box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.05);
                    border-radius: 4px;
                    padding: 10px 4px 10px 10px;
                    &>.desc{
                        margin-top: 6px;
                        font-size: 14px;
                        font-weight: 600;
                        color: #2B2B2B;
                        line-height: 16px;
                    }
                    &>.views{
                        display: flex;
                        justify-content: space-between;
                        // align-items: flex-end;
                        &>img{
                            width: 44px;
                            height: 44px;
                        }
                        &>div{
                            width: 76px;
                            height: 22px;
                            line-height: 20px;
                            border-radius: 4px;
                            font-size: 10px;
                            padding: 0px 2px;
                            transform: scale(0.8);
                            margin-right: -10px;
                            margin-top: -12px;
                            text-align: center;
                        }
                        &>.yet{
                            background: rgba(#34FF31,0.1);
                            color: #34FF31;
                        }
                        &>.no{
                            background: rgba(#888,0.1);
                            color: #bbb;
                        }
                    }
                }
                &>.trans_img{
                    width: 30px;
                    height: 30px;
                    background: rgba(#3D8BFF, 0.05);
                    border-radius: 4px;
                    text-align: center;
                    line-height: 28px;
                    &>img{
                        width: 16px;
                        height: 16px;
                    }
                }
            }
        }
    }
    .ant-select-arrow{
        margin-top: -5px;
    }
    .title_select{
        &>.title {
            font-size: 14px;
            font-family: PingFangSC-Regular, PingFang SC;
            font-weight: 400;
            color: #666666;
            margin-bottom: 15px;
        }
    
        &>.selector {
            width: 295px;
            height: 44px;
            background: #FFFFFF;
            border-radius: 4px;
            &>.ant-select-selector{
                height: 44px !important;
                line-height: 42px;
                border: 1px solid #ddd;
                &>.ant-select-selection-search{
                    &>.ant-select-selection-search-input{
                        line-height: 44px;
                    }
                }
                &>.ant-select-selection-placeholder{
                    margin-top: 6px;
                }
                .ant-select-selection-item{
                    display: flex;
                    align-items: center;
                    .form-box-icon {
                        width: 24px;
                        height: 24px;
                        margin-right: 6px;
                    }
                    .form-box-span{
                        font-weight: 600;
                        font-size: 14px;
                    }
                }
            }
        }
    }

    .form-box-icon {
        width: 16px;
        height: 18px;
        margin-right: 14px;
    }

    .cong {
        width: 14px;
        height: 20px;
        font-size: 14px;
        font-family: PingFangSC-Regular, PingFang SC;
        font-weight: 400;
        color: #666666;
        line-height: 20px;
        margin-top: 15px;
        margin-left: 20px;
    }

    .dao {
        width: 14px;
        height: 20px;
        font-size: 14px;
        font-family: PingFangSC-Regular, PingFang SC;
        font-weight: 400;
        color: #666666;
        line-height: 20px;
        margin-left: 193px;
        margin-top: -20px;
    }

    .cong-rectangle {
        width: 122px;
        height: 108px;
        background: #FFFFFF;
        box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.05);
        border-radius: 4px;
        margin-left: 20px;
        margin-top: 10px;
    }

    .yuan {
        &>img{
            width: 44px;
            height: 44px;
        }
        background: #FFFFFF;
        // border: 2px solid #EBEBEB;
        margin-top: -100px;
        margin-left: 30px;
        border-radius: 50%;
    }

    .text {
        width: 100px;
