        margin-top: -99px;
    }

    .form-box-Big {
        width: 28px;
        height: 32px;
        display: block;
        margin: 4px auto;
    }

    .form-box-bian {
        width: 16px;
        height: 18px;
        margin-left: -15px;
    }

    .form-box-bianbig {
        width: 28px;
        height: 32px;
        display: block;
        margin: 4px auto;
    }

    .text-two {
        width: 95px;
        height: 17px;
        font-size: 12px;
        font-family: PingFangSC-Semibold, PingFang SC;
        font-weight: 600;
        color: #2B2B2B;
        line-height: 17px;
        line-height: 20px;
        margin-top: 15px;

    }

    .icons {
        width: 30px;
        height: 30px;
        background: #B1232C;
        border-radius: 4px;
        opacity: 0.05;
        margin-left: 155px;
        margin-top: -30px;
    }

    .icons-one {
        width: 14px;
        height: 5px;
        margin-top: -5px;
        margin-left: 160px;
        font-size: 20px;
        color: #B1232C;
    }

    // 状态判断
    .judgment {
        width: 50px;
        height: 18px;
        background: rgba(52, 255, 49, 0.1);
        border-radius: 4px;
        margin-left: 90px;
        margin-top: -50px;

    }

    .connect {
        width: 50px;
        height: 12px;
        font-size: 10px;
        font-family: PingFangSC-Semibold, PingFang SC;
        font-weight: 600;
        color: #34FF31;
        line-height: 12px;
        opacity: 1;
    }

    .address_view{
        &>.name {
            font-size: 14px;
            font-family: PingFangSC-Regular, PingFang SC;
            font-weight: 400;
            color: #666666;
            line-height: 20px;
        }
        &>.input {
            width: 295px;
            height: 44px;
            background: #FFFFFF;
            border-radius: 4px;
            border: 1px solid #DDDDDD;
            margin-top: 10px;
        }
    }

    .shoukuan {
        display: flex;
        justify-content: space-between;
        align-items: flex-end;
        font-size: 14px;
        font-family: PingFangSC-Regular, PingFang SC;
        font-weight: 400;
        color: #666666;
        margin: 0 auto;
        margin-top: 20px;
        // margin-left: 20px;
    }

    .amount_Collection{
        height: 90px;
        margin-top: 10px;
        border-radius: 4px;
        border: 1px solid #DDDDDD;
        width: 295px;
        height: 44px;
        padding-right: 6px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        &>.Collection {
            width: 215px;
            border: 0;
            outline:none !important;
            background: #FFFFFF;
        }
    }
    

    .allbutton {
        // margin-top: -39px;
        background: rgba(#3D8BFF, 0.05);
        width: 64px;
        height: 32px;
        border-radius: 2px;
        color: #3D8BFF;
        border: none;
        margin-top: -1px;
    }

    // .allbutton button {
    //     background: rgba(#B1232C, 0.05);
    //     width: 64px;
    //     height: 32px;
    //     border-radius: 2px;
    //     color: #B1232C;
    //     border: none;
    // }

    .button_view{
        text-align: center;
        .button {
            width: 160px;
            height: 44px;
            background: #3D8BFF;
            box-shadow: 0px 4px 12px 0px rgba(177, 35, 44, 0.2);
            border-radius: 4px;
            margin: 0 auto;
            margin-top: 30px;
            border: 0;
        }
    }
    


    .lisi_res {
        height: 20px;
        font-size: 14px;
        font-weight: 600;
        color: #3D8BFF;
        margin-top: 20px;
        text-decoration: underline;
        text-align: center;
        cursor: pointer;
    }

}

