#contentText {
    display: none;
}
.detail {
    width: 335px;
    margin: 0 auto;
    margin-top: 34px;
    padding-bottom: 100px;
    & > .detail_title {
        font-weight: 600;
        & > span:first-child {
            color: #b1232c;
            font-size: 14px;
            text-decoration: underline;
            cursor: pointer;
        }
        & > span:last-child {
            color: #2b2b2b;
            font-size: 20px;
        }
    }
    & > .more_detail {
        text-align: center;
        margin-top: 32px;
        & > button {
            width: 76px;
            height: 21px;
            background: rgba(#b1232c, 0.05);
            border-radius: 4px;
            padding: 0;
            margin: 0 auto;
            border: 0;
            font-size: 12px;
            color: #b1232c;
        }
    }
    & > .pos_statistics {
        width: 335px;
        background: #fff;
        box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.05);
        border-radius: 10px;
        padding: 20px;
        margin-top: 20px;
        & > .statistics_desc {
            margin-bottom: 6px;
            & > .desc_p {
                font-size: 14px;
                font-weight: 400;
                color: #999;
            }
            & > .desc_num {
                font-size: 14px;
                font-weight: 600;
                color: #2b2b2b;
                text-align: right;
                &>img{
                    width: 20px;
                    height: 20px;
                    margin-top: -3px;
                    margin-right: 10px;
                }
            }
        }
        & > .statistics_title {
            font-size: 20px;
            font-weight: 600;
            color: #2b2b2b;
        }
        & > .statistics_list {
            padding-bottom: 20px;
            border-bottom: 1px solid #ddd;
            margin-bottom: 20px;
            &>.complete{
                text-align: right;
                padding-top: 10px;
            }
            & > .list_view {
                &>.btn{
                    width: 70px;
                    height: 18px;
                    border-radius: 4px;
                    padding: 0;
                    border: 0;
                    font-size: 12px;
                    line-height: 15px;
                }
                &>.gray{
                    background: #DDDDDD;
                    color: #fff;
                }
                &>.red{
                    background: rgba(#B1232C,0.05);
                    color: #B1232C;
                }
                & > div {
                    display: flex;
                    margin-top: -5px;
                    & > div {
                        font-size: 22px;
                        // font-family: Theater-Bold, Theater;
                        font-weight: bold;
                        color: #2b2b2b;
                        transform: scale(1, 2);
                        -ms-transform: scale(1, 2);
                        -webkit-transform: scale(1, 2);
                        -moz-transform: scale(1, 2);
                        -o-transform: scale(1, 2);
                    }
                    & > span {
                        font-size: 14px;
                        margin-top: 15px;
                        margin-left: 8px;
                        font-weight: 600;
                    }
                }
            }
        }
    }
}
